/* ------------------------------------------------------------------------------------------------ */
/* Form */
/* ------------------------------------------------------------------------------------------------ */

.form-group {}

label {
  font-size: 13px;
  line-height: 1.5em;
  color: $text;
  margin-bottom: 4px;
  font-weight: 500; }


.form-control {
  height: 40px;
  background: #F4F5F7;
  border: 1px solid #E7E9F0;
  box-sizing: border-box;
  border-radius: 10px;
  font-size: 13px;
  line-height: 20px;
  &::placeholder {
    color: #A8A8A8;
    font-size: 13px;
    line-height: 20px; }
  &:focus {
    border-color: $light;
    box-shadow: 0 0 0 0.1rem $light; } }


.custom-control {
  input {
    box-shadow: none; }
  &-label {
    padding-left: 8px;
    padding-top: 4px;
    &:before,
    &:after {
      width: 20px;
      height: 20px;
      left: -24px; } }
  .custom-control-input:checked ~ .custom-control-label::before {
    border-color: $primary;
    background-color: $primary; }

  &.custom-checkbox {
    .custom-control-label {
      &:before {
        background: #F4F5F7;
        border-color: #E7E9F0;
        border-radius: 6px; } } }

  &.custom-radio {
    .custom-control-label {
      &:before {
        border-color: #202320; }
      &:after {
        width: 12px;
        height: 12px;
        border-radius: 50%;
        top: 8px;
        left: -20px; } }
    .custom-control-input:checked ~ .custom-control-label::before {
      border-color: $secondary;
      background-color: #FFF; }
    .custom-control-input:checked ~ .custom-control-label::after {
      background: $secondary; } } }


// BS Select
.dropdown.bootstrap-select.select {
  .btn.dropdown-toggle {
    padding: 11px 16px;
    border: 1px solid #E7E9F0;
    &:focus {
      box-shadow: none;
      border: none; } } }
